
import Slider from "react-slick";
import {Box, Heading} from "@chakra-ui/react"
import SkillsData from "./SkilsData";

const SkillsImg = [

    {src:'https://sachin-kumar-keshri-portfolio.netlify.app/static/media/html_logo.2fcf94ab.png' , name : 'HTML'},
    {src:'https://cdn-icons-png.flaticon.com/512/919/919826.png' , name : 'CSS'},
    {src:'https://sachin-kumar-keshri-portfolio.netlify.app/static/media/JavaScript_Logo.2e10b2a2.png' , name : 'JavaScript'},
    {src:'data:image/png;base64,' , name : 'React'},
    {src:'https://upload.wikimedia.org/wikipedia/commons/4/49/Redux.png' , name : 'Redux'},
    {src:'data:image/png;base64,' , name : 'Styled Component'},
    {src:'data:image/png;base64,' , name : 'GitHub'},
    {src:'https://www.soat.fr/assets/images/formation/NodeJS.png' , name : 'Node.js'},
    {src:'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRCU9cs-Li0sdiiukArmYSRvahJn22BqCZXx96TxrQrtTZAUlo_VNhR02Z1G7IK226YyUE&usqp=CAU' , name : 'Visual Studio Code'},
    {src:'https://camo.githubusercontent.com/c457309037aabdce151cc0e197d6db98234a31636ef41f2cc1c339832fe20de3/68747470733a2f2f63646e2e61757468302e636f6d2f626c6f672f6c6f676f732f6e6578746a732d6c6f676f2e706e67' , name : 'Next.js'},
    {src:'https://external-preview.redd.it/bpj8UAYC3vJ1Zvve83Ai-x0_iNl-pZVaD7YswP7-jp4.jpg?width=640&crop=smart&auto=webp&s=2ff425fecc739489ea39cb1f9f7e75865508aea4' , name : 'Chakra UI'},
    {src:'https://sachin-kumar-keshri-portfolio.netlify.app/static/media/materialUi_logo.5b2d8a0a.png' , name : 'MIUI' },
    {src:'https://static-00.iconduck.com/assets.00/storybook-icon-icon-412x512-341bo8r1.png' , name : 'Story Book' },
    ]

function Skills() {
    const settings = {
        dots: false,
        infinite: true,
        slidesToShow: 5,
        slidesToScroll: 1,
        autoplay: true,
        speed: 2000,
        autoplaySpeed: 2000,
        cssEase: 'linear',
        responsive: [
            {
              breakpoint: 1024,
              settings: {
                slidesToShow: 3,
                slidesToScroll: 1,
                infinite: true,
                dots: false
              }
            },
            {
              breakpoint: 600,
              settings: {
                slidesToShow: 3,
                slidesToScroll: 1,
                initialSlide: 3
              }
            },
            {
              breakpoint: 480,
              settings: {
                slidesToShow: 1,
                slidesToScroll: 1
              }
            }
          ]
      };





  return (
    <Box id="skills" h={"fit-content"} bg={"#cbd5e0"}>
      <hr />
      <Heading textAlign={"center"} color={"blue.600"}>
        Skills
      </Heading>
      <Slider {...settings}>
        {SkillsImg.map((el) => (
          <SkillsData key={el.src} src={el.src} name={el.name} />
        ))}
      </Slider>
    </Box>
  );
}

export default Skills